* {
  transition: 0.5s ease;
  user-select: none;
}
.events {
  min-height: 100vh;
  background: radial-gradient(at left top, #667eea, #764ba2);
  animation: gradient 5s ease infinite;
  background-size: 400% 400%;
  font-family: monospace;
  text-align: center;
  padding-top: 6rem;
  font-size: 3em;
}

.eventTitle {
  font-size: smaller;
}

.tab {
  text-decoration: none;
  font-size: large;
  margin: 1rem;
  padding: 1rem;
  color: #000;
}

.is-active,
.tab:hover {
  background: rgba(227, 242, 253, 0.35);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
  border: none;
}

.cardContent {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.cardDiv {
  width: 100%;
  padding-bottom: 1rem;
}
.eventTiltDiv {
  width: 80%;
  margin: 1rem;
}

.imgDiv {
  opacity: 1;
  border-radius: 1rem;
  overflow: hidden;
}

.eventCard {
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
}
.eventCard:hover .imgDiv {
  opacity: 0.2;
}
.eventCard:hover .eventContent {
  opacity: 1;
}
.eventContent {
  width: 100%;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  font-size: 20px;
}
/* FOR Tablets */
@media only screen and (max-width: 768px) and (min-width: 414px) {
  .cardDiv {
    width: 50%;
  }
}
/* For Desktops */
@media only screen and (min-width: 768px) {
  .cardDiv {
    width: 33%;
  }
}
